.carousel {
 /*  height: 25em; */
  /* overflow:auto; */
  padding-left: 5%;
  padding-bottom: 5%;
  padding-right: 5%;
  width: 100%;
  position: relative;
  padding-top: 1rem;
}

.carousel__container {
  transition: 450ms -webkit-transform;
  transition: 450ms transform;
  transition: 450ms transform, 450ms -webkit-transform;
  font-size: 0;
  white-space: nowrap;
  margin: 70px 0px;
  padding-bottom: 10px;
}

.carousel__container:hover .carousel-item {
  opacity: 0.3;
}

.carousel__container:hover .carousel-item:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  opacity: 1;
}

